<template>
  <div>
    <keep-alive>
      <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
    <router-view v-if='!$route.meta.keepAlive'></router-view>

  </div>
</template>

<script>
export default {
  name: 'market-index',
  methods: {
    showMarketList () {
      this.$router.push({ name: 'marketList' });
    },
    showMarketDetail (itemID) {
      this.$router.push({
        name: 'marketDetail',
        params: {
          id: itemID,
        },
      })
    },
    showMarketNew () {
      this.$router.push({
        name: 'marketNew'
      })
    },
  },
  mounted () {
    this.$bus.$on('showMarketList', this.showMarketList);
    this.$bus.$on('showMarketDetail', this.showMarketDetail);
    this.$bus.$on('showMarketNew', this.showMarketNew);
  },
  beforeDestroy () {
    this.$bus.$off('showMarketList');
    this.$bus.$off('showMarketDetail');
    this.$bus.$off('showMarketNew');
  }
}
</script>

